import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import { getTurnedInMasterAssignments, getMasterDownloadLinks, updateMasterAssignments, getAssignmentSubmission, getAssignmentById, submitAssignmentFeedback } from "../backend.js"
import { ProfileIcon, DownloadIcon, CloseIcon } from "../reused-components/icons.js";
import { useHistory } from "react-router-dom";
import { Redirect, Route } from "react-router-dom"
import Slider from 'react-input-slider';
import { AssignmentStatus, openInNewTab } from "./HomePage/ChallengePopUp.js";
import { currentStudent, currentAssignment } from "../reused-components/TeacherComponents.js";
import { openPopup, closePopup } from "../reused-components/Popup";
import { updatePoints } from "../reused-components/pointTrackers.js";

export class TeacherSingleAssignmentPage extends React.Component {
  constructor(props) {
    super(props)
    this.studentOrTeacher = "t"
    this.state = { comments: [], currentComment: "", grade: null, assignmentId: null, assignmentName: null, key:true }
    //this.loadMasterAssignment()
    this.getSubmissionInfo()
  }
  /*
  outdated method
  loadMasterAssignment = async () => {
    var assignments  = await getTurnedInMasterAssignments()
    var assignment;
    console.log(assignments, this.props.checkpoint, this.props.studentName)
    for (var i = 0; i < assignments.length; i++) {
      
      if (assignments[i].checkpoint === this.props.checkpoint) {
        
        if (assignments[i].student_name === this.props.studentName) {
          
          assignment = assignments[i]
      }
    }
    
  }
  if (!assignment) {
    return
  }
  console.log(assignment)
  this.setState({comments: assignment.comments, grade: assignment.grade, assignmentId: assignment.id})
}*/
  getSubmissionInfo = async () => {
    var submission = await getAssignmentSubmission(currentStudent, currentAssignment)
    if (!submission) {
        window.location.replace("#/teacher/class/assignment")
        return
    }
    if (submission.comments) {
      this.setState({comments: JSON.parse(submission.comments)})
    }
    this.points = submission.points
    this.setState({assignmentName: submission.challenge_name, status: submission.status, points: submission.points, 
      assignmentUploadIDs: submission.assignment_id_list,
      date: submission.submission_date, id: submission.id,
      key: !this.state.key})
  }
  addComment = (e) => {
    if (e !== 13 || this.state.comment === "") {
      return
    }
    var comments = this.state.comments;
    comments.push(this.studentOrTeacher + this.state.comment)

    updateMasterAssignments(currentStudent, currentAssignment, JSON.stringify(comments), this.points)
    this.setState({ comments: comments, comment: "" })
  }
  changeCurrentComment = (comment) => {
    this.setState({ comment: comment })
  }
  render() {
    return (
      <div key={this.state.key}>
        <TeacherNavbar />
        <div style={{ paddingTop: "3vh", paddingLeft: "9vw", position: "relative" }}>
        <div style={{ right: 0,  position: "fixed", marginTop: "5vh"}}><SingleAssignmentCompleteOverview
        updatePoints={(points => {
          this.points = points
        })}
            changeState={state => {
              state.key = this.state.key
              this.setState(state)}}
            state={this.state}/></div>
          <h1 style={{ display: "inline-block", width: "45vw", fontSize: 25 }}>
            {this.state.assignmentName}</h1>
          <div style={{ height: "50vh", width: "45vw", position: "absolute", display: "flex", justifyContent: "flex-end", flexDirection: "column", overflowY: "auto" }}>
            {this.state.comments.map(comment => <div style={{ marginTop: "5%" }}>{((comment[0] === this.studentOrTeacher) ?
              <div style={{ backgroundColor: "#e8e8e8", width: "45%", borderRadius: "10px", height: "auto" }}><div style={{ padding: "5%" }}>{comment.substring(1)}</div></div> :
              <div style={{ backgroundColor: "#e8e8e8", width: "45%", marginLeft: "55%", borderRadius: "10px" }}><div style={{ padding: "5%" }}>{comment.substring(1)}</div></div>)}</div>)}
            <div style={{ width: "45vw", height: "10vh", backgroundColor: "#F8F8F8", bottom: "7vh", position: "fixed", display: "flex", alignContent: "center", borderRadius: "2vh" , alignItems: "center"}}>
              <ProfileIcon /> <div style={{ marginTop: "-10px" }}><input style={{ width: "30vw" }} placeholder="Write comment here...." onChange={(comment) => this.changeCurrentComment(comment.target.value)}
                onKeyDown={(e) => this.addComment(e.keyCode)} value={this.state.comment}
              />
              
              </div>
              <div >
              <button className="add-task-button" onClick={() => this.addComment(13)} style={{marginLeft: "10px"}}>
                <div className="add-button-text">
                  Send Feedback
                </div>
              </button>
              </div>
            </div>
          </div>
          
        </div>
      </div>
    )
  }
}


