class AssignmentPointsSlider extends React.Component {
  constructor(props) {
    super(props)
    this.state = { pointValue: this.props.points * 10 }
  }
  render() {
    return (
      <div style={{ position: "relative", marginTop: "60px" }}>
        <div style={{ fontWeight: 600, fontSize: 20 }}>
          Score ({this.props.points}/10)
        </div>
        <div style={{ marginTop: "20px" }}>
          <Slider
            axis="x"
            styles={{
              track: { width: 300, height: 25, borderRadius: 40 },
              active: { borderRadius: 40 },
              thumb: { width: 30, height: 30 }
            }}
            x={this.state.pointValue}
            onChange={({ x }) => {
              this.props.changePoints(Math.floor(x / 10))
              this.setState({ pointValue: x })
            }}
          /></div>
      </div>

    )
  }
}


function formatDate(date) {
  //alert(date)
  var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate();
  if (month.length < 2) 
      month = '0' + month;
  if (day.length < 2) 
      day = '0' + day;
  return `${month}/${day}`;
}

class SingleAssignmentCompleteOverview extends React.Component {
  constructor(props) {
    super(props)
    this.state = { assignmentDownloadLinks: [], points: this.props.points }
    this.updateDownloadMasterLinks()

  }
  submitFeedback = async () => {
    //unfinished method
    this.props.updatePoints(this.props.state.points)
    submitAssignmentFeedback({points: this.props.state.points, challenge_name: currentAssignment, comments: JSON.stringify(this.props.state.comments), username: currentStudent})
  }
  updateDownloadMasterLinks = async () => {
    var links
    links = await getMasterDownloadLinks()
    var linkList = []
    for (var i = 0; i < links.length; i++) {
      if (links[i].checkpoint === this.props.checkpoint) {
        if (links[i].name === this.props.studentName) {
          linkList.push(links[i].file)
        }
      }

    }
    console.log(linkList)
    this.setState({ assignmentDownloadLinks: linkList })
  }
  downloading = () => {
    //window.location.replace('https://www.google.com')
    this.state.assignmentDownloadLinks.forEach((link) => {
      console.log(link)
      window.location.replace(link)
    })
  }
  render() {
    return (<>
      <div style={{
        position: "absolute", right: "6vw",
        height: "calc(100vh - 105px - 5vh)", width: "33vw", borderLeft: "2px solid grey", paddingLeft: "2vw", marginTop: "-40px"
      }}>
        <div style={{ position: "relative", height: "100%", width: "100%" }}>
          <h1>View Work</h1>
          <div style={{
            width: "85%", height: "10%", alignItems: "center", display: "flex", position: "relative"
            , cursor: "pointer", marginTop: "50px"
          }} onClick={this.downloading}>
            <div style={{ alignItems: "center", display: "flex", fontWeight: 600, fontSize: 18 }}>
              Last changed {formatDate(this.props.state.date)}

            </div>
            <div style={{ position: "absolute", right: "3%" }}>
              <button className="add-task-button" style={{ marginLeft: "auto" }}
              onClick={() => {
                openPopup(<ViewSubmissionPopUp assignmentUploadIDs={this.props.state.assignmentUploadIDs}/>)
              }}
              ><div className="add-button-text">View Submission</div></button>
            </div>
          </div><div style={{ marginTop: "60px", fontSize: 20, fontWeight: 600 }}>
            Status <div style={{ backgroundColor: "#f8f8f8", borderRadius: "10px", width: "153px", overflow: "hidden", marginTop: "20px" }}>
              {["approved", "failed", "submitted"].map((status, index) =>
                <div style={{
                  borderLeft: (index === 0 ? null : "1px solid grey"), display: "inline-block", padding: "5px",
                  backgroundColor: (status === null ? "grey" : null), cursor: "pointer", backgroundColor: (this.props.state.status === status ? "#e8e8e8" : null)
                }}
                  onClick={() => {
                    //Add onlick
                    this.props.changeState({status: status})
                  }}
                >
                  <AssignmentStatus status={status} iconSize={40} />
                </div>
              )}
            </div>
          </div>
          {/*<div style={{width: "85%", height: "40%", backgroundColor: "#F8F8F8", marginTop: "5%", borderRadius: "2vh"}}>
        <div style={{padding: "3%"}}>
      <div style={{fontWeight: "bold"}}>Points</div>
      <div><input style={{height: "15px", width: "15px"}} type="checkbox"
                          defaultChecked={true} />50 Points: Write Assignment </div>
                          <div><input style={{height: "15px", width: "15px"}} type="checkbox"
                          defaultChecked={true} />50 Points: Write Assignment </div>
                          </div>

  </div>*/}
          <AssignmentPointsSlider points={this.props.state.points} changePoints={(points) => this.props.changeState({ points: points })} />
          <button className="add-task-button" style={{marginTop: "20px"}}
          onClick={() => {
            
            this.submitFeedback()
            openPopup(<FeedbackSubmissionConfirmationPopUp />)
          }}
          >
            <div className="add-button-text">
            Save Status
            </div>
          </button>
        </div>
      </div>
    </>
    )
  }
}




class ViewSubmissionPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {uploads: []}
    this.getUploads()
  }
  getUploads = async () => {
      var uploads = []
      for (var i = 0; i < this.props.assignmentUploadIDs.length; i++) {
      var upload = await getAssignmentById(this.props.assignmentUploadIDs[i])
      uploads.push({name: upload.file_name, downloadURL: (upload.url_submissions ? upload.url_submissions: upload.file)})
    }
    this.setState({uploads: uploads})
  }
  renderUploads = ()  => {
    
    return (<div style={{ marginBottom: "20px", paddingLeft: "30px", paddingRight: "30px"}}>
      {
        
        this.state.uploads.map(upload => {
          //alert(JSON.stringify(upload))
          return <div style={{width: "100%"}}>{upload.name}
          <button style={{float: "right"}} className="add-task-button"
          onClick={
            () => {
              openInNewTab(upload.downloadURL)
            }
          }
          >
            <div className="add-button-text">
            Open
            </div>
            
            </button>

          </div>
        })

      }</div>)
  }
  render() {
    return (
    <div style={{minWidth: "30vw", zIndex: 2, maxWidth: "800px"}} key={JSON.stringify(this.state.uploads)}>
    <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
      <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={closePopup}>
        <CloseIcon color="white"/></button>
      <h1 style={{ marginLeft: "20px", color: "white" }}>View Uploads</h1></div>
    <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px", maxWidth: "85vw"}}>
      {this.renderUploads()}
    </div>
    </div>
    )
  }
}


class FeedbackSubmissionConfirmationPopUp extends React.Component {
  render(){
    return (<div style={{padding: "15px", borderRadius: "20px", backgroundColor: "white", zIndex: 2}}>
      
      Status has been updated!
      <div style={{marginTop: "10px"}}>
      <button
      className="add-task-button"
      onClick={() => {
        
        closePopup()
        window.location = "#/teacher/class/assignment/"
      }}
      >
      <div className="add-button-text">Okay</div>
      </button>
</div>
    </div>)
  }
}