export class AssignmentFeedbackPopUp extends React.Component {
  render() {
    
    var currentAssignmentChecklist = findAssignmentStatus(this.props.assignmentResults, this.props.currentChallenge.challenge_name)
    try{
    var comments = JSON.parse(currentAssignmentChecklist.comments)}
    catch {
      var comments = [] 
      /*
      alert(JSON.stringify(this.props.currentChallenge))
      alert(JSON.stringify(currentAssignmentChecklist))
      */
    }
    return <div style={{ zIndex: 2 }}>
      <div style={{ backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
        <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
          <CloseIcon color="white" /></button>
        <h1 style={{ marginLeft: "20px", color: "white" }}>
          {//<MasterIcon size="40" colors={["white","#965594"]/>
          }Feedback</h1></div>
      <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px", paddingRight: "30px" }}>
        <div style={{ position: "relative", marginLeft: "30px", display: "inline-block" }}>

          <FeedbackCommentBox comments={comments} />
        </div>
        <div style={{ display: "inline-block", marginLeft: "30px", height: "100%" }}>
          <div>
            <AssignmentInformationBox currentChecklist={currentAssignmentChecklist} />
          </div>
          <div style={{ marginTop: "20px", }} >
            <button className="add-task-button" style={{ display: "inline-block" }} onClick={this.props.resubmit}>
              <div className="add-button-text">
                Resubmit</div>
            </button>
            <button className="add-task-button" style={{ display: "inline-block", marginLeft: "20px" }}
              onClick={() => {
                //Add function to view submitted files
                this.props.viewSubmission()
                this.props.resubmit()
              }}
            >
              <div className="add-button-text">
                View Submission</div>
            </button>
          </div>
        </div>

      </div>
    </div>
  }
}


class FeedbackCommentBox extends React.Component {
  constructor(props) {
    super(props)
    this.state = { comments: this.props.comments, currentComment: "" }
    //Add get function to get comments from database
  }
  render() {
    return <div style={{ minWidth: "300px", maxWidth: "500px" }}>
      <div style={{ width: "100%", overflowY: "auto", maxHeight: "45vh" }}>
        {this.state.comments.map(comment => {
          if (comment.substring(0, 1) === "t") {
            var pos = "0"
          }
          else {
            var pos = "auto"
          }
          return <><div style={{ padding: "10px", borderRadius: "10px", backgroundColor: "#f9f9f9", marginLeft: pos, width: `${(comment.length - 1) * 8}px`, maxWidth: "70%" }}>
            {comment.substring(1)}
          </div><br></br></>
        })}
      </div>
      <input placeholder="comment" value={this.state.currentComment} onChange={(text) => { this.setState({ currentComment: text.target.value }) }}

        onKeyDown={(e) => {
          if (e.keyCode === 13) {
            var comments = this.state.comments
            comments.push("s" + this.state.currentComment)
            //Add patch request to update database
            this.setState({ currentComment: "", comments: comments })
          }
        }}
        style={{ width: "100%" }}></input>
    </div>
  }
}


class AssignmentInformationBox extends React.Component {
  render() {
    var submissionDate = new Date(this.props.currentChecklist.submission_date)
    return <div style={{ padding: "10px", backgroundColor: "#f9f9f9", lineHeight: "30px", borderRadius: "20px" }}>
      {//Due Date: 18:15 3/23<br></br>
      }
      Submitted: {submissionDate.getHours()}:{submissionDate.getMinutes()} {submissionDate.getMonth() + 1}/{submissionDate.getDate()}<br></br>
      Points: {this.props.currentChecklist.points}/10<br></br>
      Status: <div style={{ transform: "translateY(8px)", display: "inline-block" }}><AssignmentStatus status={this.props.currentChecklist.status} />   </div>
    </div>
  }
}