function openInNewTab(href) {
  Object.assign(document.createElement('a'), {
    target: '_blank',
    href: href,
  }).click();
}
class PortfolioMasterPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended", assignments: [] }
    this.updateAssignments()
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({ isExtended: "not-extended" })
    }
    else {
      this.setState({ isExtended: "extended" })
    }
  }
  updateAssignments = async () => {
    var assignments = await getMasterDownloadLinks()
    this.setState({ assignments: assignments })
  }
  render() {
    var tempAssignments = this.state.assignments
    tempAssignments.reverse()
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Your Portfolio" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
          </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : { width: "100vw" })}>
            <div style={{ marginLeft: "40px" }}>
              <PointTrackers />
            </div>
            <div style={{ width: "100%", height: "93%", position: "relative", marginTop: "3%" }}>
              <div style={{ width: "70%", height: "70%", marginTop: "2%", }}>
                <h1 style={{ marginLeft: "3%", }}>
                  Previous Assignments
                </h1>
                <div style={{ marginLeft: "3%", }}>
                  <div style={{ marginBottom: "30px", cursor: "pointer" }} onClick={() => this.props.changePage("main")}><ReturnIcon size="40px" /></div>
                  <div className='teacher-overview-outer-div' style={{ height: "50vh", overflowX: "auto" }}>
                    {((tempAssignments.length !== 0) ? tempAssignments.map(el =>
                      <div className="teacher-overview-item" onClick={() => {
                        if (el.url_submissions) {
                          openInNewTab(el.url_submissions)
                          return
                        }
                        window.location.replace(el.file)
                      }}> {el.file_name}
                        <div className="add-task-button" style={{ position: "absolute", right: "5%", color: "white", paddingLeft: "5px", paddingRight: "5px" }}>Download</div>
                      </div>
                    ) : <h1 style={{ marginLeft: "40%", marginTop: "100px" }}>Loading...</h1>)}
                  </div>
                  {//<TeacherOverviewList type="assignment" link="/teacher/class/" suffix={"/"+username}/>
                  }
                </div>
              </div>
              <CheckList />
            </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}


class PortfolioDownloadPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended", downloads: [] }
    this.updateDownloads()
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({ isExtended: "not-extended" })
    }
    else {
      this.setState({ isExtended: "extended" })
    }
  }
  updateDownloads = async () => {
    var downloadData = await getAllDownloadData()
    var downloads = []
    downloadData.forEach((download) => {

      download["download_titles"].forEach((item, index) => {
        var newItem = {}
        newItem["filename"] = item
        newItem["file"] = download["download_urls"][index]
        downloads.push(newItem)
      })
    })
    this.setState({ downloads: downloads })
  }
  render() {
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Your Portfolio" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
          </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : { width: "100vw" })}>
            <div style={{ marginLeft: "40px" }}>
              <PointTrackers />
            </div>
            <div style={{ width: "100%", height: "93%", position: "relative", marginTop: "3%" }}>
              <div style={{ width: "70%", height: "70%", marginTop: "2%", }}>
                <h1 style={{ marginLeft: "3%", }}>
                  Previous Resources
                </h1>
                <div style={{ marginLeft: "3%", }}>
                  <div style={{ marginBottom: "30px", cursor: "pointer" }} onClick={() => this.props.changePage("main")}><ReturnIcon size="40px" /></div>
                  <div className='teacher-overview-outer-div' style={{ height: "50vh", overflowX: "auto" }}>
                    {((this.state.downloads.length !== 0) ? this.state.downloads.map(el =>
                      <div className="teacher-overview-item" onClick={() => window.location.replace(el.file)}>{el.filename}
                        <div className="add-task-button" style={{ position: "absolute", right: "5%", color: "white", paddingLeft: "5px", paddingRight: "5px" }}>Open Link</div>
                      </div>
                    ) :
                      <h1 style={{ marginLeft: "40%", marginTop: "100px" }}>Loading...</h1>
                    )}
                  </div>
                  {//<DownloadOverviewList changePage={this.props.changePage}/>
                  }
                </div>
              </div>
              <CheckList />
            </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}

class DownloadOverviewList extends React.Component {
  constructor(props) {
    super(props)
    this.state = { checkpoints: ["Checkpoint 1", "Checkpoint 2", "Checkpoint 3"], checkpointIndex: null, lessonList: ["Lesson 1"], type: "lesson", downloads: [[{ name: "Download file 1", link: "https://journey.ml/" }]] }
  }
  handleItemClick = (index) => {
    if (this.state.type === "lesson") {
      this.setState({ type: "download", downloadIndex: index })
      return
    }
    else if (this.state.type === "download") {
      window.location.replace(this.state.downloads[this.state.downloadIndex][index].link)
    }
  }
  handleReturn = () => {
    if (this.state.checkpointIndex !== null) {
      this.setState({ checkpointIndex: null })
      return
    }
    else if (this.state.type === "lesson") {
      this.props.changePage("main")
    }
    else if (this.state.type === "download") {
      this.setState({ type: "lesson" })
    }
  }
  render() {
    var displayList = this.state.lessonList
    if (this.state.type === "download") {
      displayList = this.state.downloads[this.state.downloadIndex].map(item => item.name)
    }
    return (<>
      <div style={{ marginBottom: "30px", cursor: "pointer" }} onClick={this.handleReturn}><ReturnIcon size="40px" /></div>
      <div className="teacher-overview-outer-div" style={{ height: "50vh" }}>
        <div>
          {this.state.checkpoints.map((checkpoint, checkpointIndex) =>
            <div className={(this.state.checkpointIndex === checkpointIndex) ? "checkpoint-portfolio-download" : "checkpoint-portfolio-download-closed"} style={{ backgroundColor: "transparent" }}>
              <div className="teacher-overview-item"
                onClick={() => this.setState({ checkpointIndex: checkpointIndex })}
              ><div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}

              >{checkpoint}</div></div>
              {(this.state.checkpointIndex === checkpointIndex) ? displayList.map((item, index) => <div className="teacher-overview-item" style={{ marginLeft: "40px" }} onClick={() => this.handleItemClick(index)}>
                {(this.state.type === "lesson") ? <LessonIcon /> : <DownloadIcon />}<div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }} >{item}</div>
                {(this.state.type === "download") ?
                  <button style={{
                    backgroundColor: "#965594",
                    color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer", position: "absolute", right: "5%"
                  }}> Download </button>
                  : null}
              </div>) : null}</div>)} </div>
      </div>
    </>
    )
  }
}

class CheckList extends React.Component {
  render() {
    return (
      <div style={{ position: "absolute", height: "85%", width: "22%", backgroundColor: "#f1f1f1", top: 0, right: "3%", paddingTop: "20px", borderRadius: "20px" }}>
        <div style={{ display: "flex", justifyContent: "center", height: "100%", width: "100%" }}>
          <div style={{ width: "90%", height: "100%" }}>
            <div style={{ width: "100%", height: "60px", borderRadius: "20px 20px 0 0", backgroundColor: "#8B4389", display: "block" }}>
              <div style={{ padding: "20px", color: "white" }}>
                Checklist
              </div>
            </div>
            <div style={{ width: "100%", height: "80%", backgroundColor: "white", borderRadius: "0 0 20px 20px" }}>
              <div style={{ padding: "20px" }}>
                {["1 Pager", "Pitch Deck", "Prototype", "Startup Resume", "Business Plan"].map(item => <div style={{ marginBottom: "20px" }}><input style={{ height: "20px", width: "20px" }} type="checkbox"
                  defaultChecked={false} /> {item}
                </div>)}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}