var screenAspectRatio = window.innerHeight / window.innerWidth
var narrowBrowser = screenAspectRatio > 0.55;




export class HomePage extends React.Component {
  constructor(props) {
    super(props)
    window.addEventListener("resize", () => this.changedBrowserSize())
    this.screenAspectRatio = window.innerHeight / window.innerWidth
    narrowBrowser = this.screenAspectRatio > 0.55;
    if (mobileVersion) {
      narrowBrowser = false
    }
    this.state = { isExtended: ((mobileVersion || narrowBrowser) ? "not-extended" : "extended"), popUp: false, narrowBrowser: narrowBrowser }
    //getRequestToDoList()

    this.refreshDownloadData();
  }
  changedBrowserSize = () => {
    this.screenAspectRatio = window.innerHeight / window.innerWidth
    narrowBrowser = this.screenAspectRatio > 0.55;
    if (this.screenAspectRatio > 0.77) {
      mobileVersion = true
    }
    else if (!isMobile && this.screenAspectRatio < 0.77) {
      mobileVersion = false
    }
    this.setState({ narrowBrowser: narrowBrowser })
  }
  refreshDownloadData = async () => {
    var downloadData = await loadDownloadData();

    this.setState((props, state) => {
      return ({ downloadData: downloadData })
    })
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended" }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended" }
      })
    }
  }
  changePopUp = (type = "none") => {
    this.setState({ popUp: !this.state.popUp, popUpType: type, videoQuestionData: null })
  }
  openVideoQuestionPopup = (type = "none", data, unpauseVideo) => {
    this.unpauseVideo = unpauseVideo
    this.setState({ popUp: true, videoQuestionData: data, popUpType: type })
  }
  popUp() {
    if (!this.state.popUp) {
      return;
    }
    if (this.state.videoQuestionData) {
      return (<PopUp changePopUp={this.changePopUp} type={this.state.popUpType} videoQuestionData={this.state.videoQuestionData} unpauseVideo={this.unpauseVideo}
      />)
    }
    return (<PopUp changePopUp={this.changePopUp} type={this.state.popUpType} downloadData={this.state.downloadData}
    />)
  }
  render() {
    this.screenAspectRatio = window.innerHeight / window.innerWidth
    var loadingBarList = [1, 3, 2];
    var width = "50vw";
    return (
      <div style={mobileVersion ? { overflowY: "scroll" } : {}} key={JSON.stringify(this.state.narrowBrowser)}>

        <div style={{ position: "fixed" }}><Navbar pageName="Home" sideNavAnimation={this.changeState} /></div>
        <div style={{ position: "fixed", zIndex: -1 }} className={this.state.isExtended}>
          <div style={(mobileVersion || narrowBrowser) ? { display: "block", position: "absolute", left: "0", zIndex: 10 } : { display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={this.state.isExtended} /></div>
          <div className="video-outer-div" style={mobileVersion ? {
            position: "fixed", bottom: "auto", top: "25vh", right: "5vw", overflowY: "scroll"
            , height: "calc( 100vh - 50px)",
          } : (narrowBrowser ? { position: "fixed", left: "-200px", bottom: "0" } : null)}>
            <div style={{ paddingBottom: "5vh" }}>
              <PointTrackers />
              <div style={mobileVersion ? { width: "90vw", aspectRatio: "16/9" } : { width: (narrowBrowser ? "65vw" : width.toString()), aspectRatio: "16/9", maxHeight: "60vh" }}>
                <VideoPlayer openVideoQuestionPopup={this.openVideoQuestionPopup} />
              </div>
              <div style={mobileVersion ? { width: "100%" } : { width: width.toString, marginTop: "calc(4vh - 20px)" }}>
                <ChangeHomePageVideo />
                <div style={mobileVersion ? { display: "block" } : { display: "inline-block", float: "right" }}>
                  <div style={mobileVersion ? { backgroundColor: "#E8E8E8", height: "13vh", width: "90vw", marginTop: "2vh", borderRadius: "2vh" }
                    : { backgroundColor: "#f4f4f4", height: "13vh", width: (narrowBrowser ? "40vw" : "30vw"), marginTop: "2vh", borderRadius: "2vh" }}>
                    <div style={{ justifyContent: "space-evenly", display: "flex", transform: "translateY(1.5vh)" }}>
                      <button onClick={() => this.changePopUp("download")} style={{ cursor: "pointer" }}>
                        <DCMButton size="20px" loadingBarCompletion={loadingBarList[0]} text="Resources" icon={<DownloadIcon size="4vh" color="white" />} narrowBrowser={this.narrowBrowser} />
                      </button>
                      <button style={{ cursor: "pointer" }} onClick={() => this.changePopUp("promptOverview")}>
                        <DCMButton size="100px" loadingBarCompletion={loadingBarList[2]} text="Prompts" icon={<MasterIcon size="3.5vh" colors={["white", "#965594"]} narrowBrowser={this.narrowBrowser} />} />
                      </button>
                      <button onClick={() => this.changePopUp("challenge")} style={{ cursor: "pointer" }}>
                        <DCMButton size="100px" loadingBarCompletion={loadingBarList[1]} text="Challenges" icon={<HatchetIcon size="3vh" colors={["white", "#965594"]} narrowBrowser={this.narrowBrowser} />
                        } />
                      </button>                    

                    </div>
                  </div>
                </div>
              </div>
              {mobileVersion ? <div style={{ marginTop: "2vh" }}><InformationField changePopUp={this.changePopUp} downloadData={this.state.downloadData} /></div> : null}
            </div>
          </div>
        </div>
        {mobileVersion ? null : <InformationField changePopUp={this.changePopUp} downloadData={this.state.downloadData} />}
        {this.popUp()}
      </div>
    )
  }
}

class CompanyInfo extends React.Component {
  constructor(props) {
    super(props)
    this.state = { fullName: null }
    this.getFullName()
  }
  getFullName = async () => {
    var fullName = await getUserFullName()
    this.setState({ fullName: fullName })
  }
  render() {
    return <>
      <div style={{ display: "inline-block" }}>
        <h1 style={{ fontSize: "3vh" }}>Startup: Company inc.</h1>
        <h1 style={{ fontSize: "2vh" }}>Entrepreneur:   {this.state.fullName}</h1>
      </div></>
  }
}

class ChangeHomePageVideo extends React.Component {
  render() {
    return <div style={{ display: "inline-block", margin: "1vw", backgroundColor: "#f4f4f4", borderRadius: "2vh", overflow: "hidden", marginBottom: "0"}}>
      <div style={{ display: "inline-block", textAlign: "center", cursor: "pointer", borderRight: "2px solid #e8e8e8", padding: "1vh" }} className="dropdown-menu-item" onClick={() => changeVideoUrlIndex(-1)}>
        <SkipBackwardIcon size="4vh" />
        <div>
          Previous Video
        </div>
      </div>
      <div style={{ display: "inline-block", textAlign: "center", cursor: "pointer", padding: "1vh"  }} className="dropdown-menu-item" onClick={() => changeVideoUrlIndex(1)}>
        <SkipForwardIcon size="4vh" />
        <div>
          Next Video
        </div>
      </div>


    </div>
  }
}

class PopUp extends React.Component {
  constructor(props) {
    super(props)
    this.state = { type: this.props.type }
  }
  newPopUp = (type) => {
    this.setState({ type: type })
  }
  render() {
    return <div className="popup-background-div">
      <div className="popup-clickable-background-div" onClick={(this.state.type === "videoQuestion") ? null : this.props.changePopUp}>
      </div>
      {(this.state.type === "challenge") ? <ChallengePopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData}
        newPopUp={this.newPopUp}
      /> : null}
      {//(this.state.type === "master") ? <MasterPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null
      }
      {(this.state.type === "promptOverview") ? <PromptOverviewPopUp downloadData={this.props.downloadData} changePopUp={this.props.changePopUp} /> : null}
      {(this.state.type === "download") ? <DownloadPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.state.type === "knowledgeNuggets") ? <KnowledgeNuggetsPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.state.type === "fromTheArchitects") ? <FromTheArchitectsPopUp changePopUp={this.props.changePopUp} downloadData={this.props.downloadData} /> : null}
      {(this.state.type === "videoQuestion") ? <VideoQuestionPopup
        changePopUp={() => {
          this.props.unpauseVideo()
          this.props.changePopUp()
        }} videoQuestionData={this.props.videoQuestionData} /> : null}
    </div>
  }
}



export class DownloadPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { downloadData: this.props.downloadData }
  }
  renderDownload() {
    //Have to change code when download data api gets updated from string to list.
    return this.state.downloadData.download_titles.map((text, index) => (<div style={{ marginBottom: "20px", width: "100%" }}>
      <p1 style={{ marginLeft: "30px", paddingRight: "30px" }}>
        {text}</p1><a href={this.state.downloadData.download_urls[index]} target="_blank"><button style={{
          backgroundColor: "#965594",
          color: "white", marginRight: "30px", padding: "7px", borderRadius: "5px", cursor: "pointer", float: "right"
        }}

        >Open Link</button></a></div>))
  }
  render() {
    return (<div style={{ minWidth: "30vw", zIndex: 2 }}>
      <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
        <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={() => {
          this.props.changePopUp()
          closePopup()
        }}>
          <CloseIcon color="white" /></button>
        <h1 style={{ marginLeft: "20px", color: "white" }}><DownloadIcon color="white" />Resources</h1></div>
      <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "30px", paddingTop: "50px" }}>
        {this.renderDownload()}

        {/* <div style={{ position: "absolute" }}>
        
        </div> */}
      </div>
    </div>
    )
  }
}

var changeVideoUrlIndex;
class VideoPlayer extends React.Component {
  constructor(props) {
    super(props)
    this.state = { video_url: null, playing: false, index: 0, videoUrlIndex: 0, quizes: [] }

    //Change this stuff later when seb gives you the stuff
    this.refreshVideoPlayer()
    changeVideoUrlIndex = (changeDirection) => {
      
        var newVideoUrlIndex = this.state.videoUrlIndex
        newVideoUrlIndex += changeDirection
        if (newVideoUrlIndex < this.state.video_urls.length && newVideoUrlIndex >= 0) {
          this.setState({videoUrlIndex: newVideoUrlIndex, video_url: this.state.video_urls[newVideoUrlIndex] })
        }
    } 

  }
  removecompletedPrompt = async () => {
    var completed = await checkIfPromptCompleted()
    completed = completed.map(item => item.promptName)
    var newQuizes = []
    this.state.quizes.forEach(item => {
      if (!completed.includes(item.title)) {
        newQuizes.push(item)
      }
    })
    //alert(JSON.stringify(newQuizes))
    this.setState({ quizes: newQuizes })
  }
  refreshVideoPlayer = async () => {
    var newState = await loadDownloadData()
    if (newState.video_urls) {
      newState.video_url = newState.video_urls[0]
    }
    
    //alert(newState.video_download_prompts)
    newState.quizes = JSON.parse(newState.video_download_prompts)
    //alert(newState.quizes)
    this.setState((props, state) => {
      return newState
    })
    //this.removecompletedPrompt()
  }
  handleVideoProgress = (time) => {
    /*if (!this.quizes[this.state.index]) {
      return
    }*/
    this.state.quizes.forEach((item, i) => {
      if ((time.playedSeconds > item.start) && (time.playedSeconds < this.state.quizes[i].start + 1) && !this.state.quizes[i].completed) {
        this.props.openVideoQuestionPopup("videoQuestion", this.state.quizes[i], this.unpauseVideo)
        this.state.quizes[i].completed = true;
        this.setState({
          playing: false,// index: this.state.index + 1
        })
      }
    })
  }
  unpauseVideo = () => {
    this.setState({ playing: true })
  }
  render() {

    return (<>
      {(this.state.video_url ?
        <ReactPlayer url={this.state.video_url} width="100%" height="100%" controls={true} playing={this.state.playing}
          light={this.state.video_url.substr(-3) === "png" ? this.state.video_url : null}
          onProgress={(time) => {
            /*if (!this.quizes[this.state.index]) {
              return
            }*/
            this.state.quizes.forEach((item, i) => {
              //alert(item)
              if ((time.playedSeconds > item.start) && (time.playedSeconds < this.state.quizes[i].start + 1) && !this.state.quizes[i].completed) {
                this.props.openVideoQuestionPopup("videoQuestion", this.state.quizes[i], this.unpauseVideo)
                this.state.quizes[i].completed = true;
                this.setState({
                  playing: false,// index: this.state.index + 1
                })
              }
            })
          }}
          onEnded={() => {/*
          this.setState({video_url: null})
          this.refreshVideoPlayer()*/
          }}
          onPlay={() => {
            this.setState({ playing: true })
          }}
          onPause={() => this.setState({ playing: false })}

        />

        : <div style={{
          width: "100%", height: "100%", display: "flex",
          justifyContent: "center", alignItems: "center", backgroundColor: "black"

        }}><div style={{ height: "25%", width: "15%" }}><Loader type="Oval" color="#00BFFF" height={"100%"} width={"100%"} />

          </div></div>)}
    </>)
  }
}
