//Used to determine student and assignment in TeacherSingleAssignmentOverview
export var currentStudent;
export var currentAssignment;

export class TeacherOverviewList extends React.Component {
  constructor(props) {
    super(props)
    this.type = this.props.type
    this.state = { overviewList: [], checkpoints: checkpointList }
    if (this.type === "student") {
      this.updateStudentList()
      this.icon = <StudentIcon />
    }
    else if (this.type === "assignment") {
      //this.updateAssignmentList()
      this.icon = <AssignmentIcon />
    }
    else if (this.type === "lesson") {
      this.updateLessonList()
      this.icon = <AssignmentIcon />
    }
  }
  renderRightField = (item) => {
    if (this.type === "assignment") {
      return;
      //Not needed atm, was just used earlier for clarifying the difference between assignment and student
      return <div style={{ position: "absolute", right: "5%" }}>Due 15:00 21/10</div>
    }

    else if (this.props.showMissed) {
      return
      return <div style={{ position: "absolute", right: "5%" }}>0</div>
    }

  }
  updateAssignmentList = async (id) => {
    this.type = "assignment"
    var assignmentList = await getAssignmentNames(id);
    this.setState({ overviewList: assignmentList })
  }
  updateLessonList = async () => {
    //method not completed yet
    var lessonList = await getLessonNames()
    this.setState({ overviewList: lessonList })
  }
  updateStudentList = async () => {
    var studentList = await getClassInfo()
    console.log(studentList)
    this.setState({ overviewList: studentList })
  }
  updateStudentAssignment = (item) => {
    var tempState = { checkpoint: this.state.checkpointIndex + 1 }
    tempState[this.type] = item
    this.setState(tempState)
  }
  render() {
    var suffix = ""
    if (this.props.suffix) {
      suffix = this.props.suffix
    }
    if (this.type === "lesson") {
      return (<div className="teacher-overview-outer-div" style={{ height: "50vh" }}>
        <div>
          {this.state.checkpoints.map((checkpoint, checkpointIndex) =>
            <div className={(this.state.checkpointIndex === checkpointIndex) ? "checkpoint-portfolio-download" : "checkpoint-portfolio-download-closed"} style={{ backgroundColor: "transparent" }}>
              <div className="teacher-overview-item"
                onClick={async () => {
                  await this.updateLessonList()
                  this.type = "lesson"
                  this.setState({ checkpointIndex: checkpointIndex })
                }}
              ><div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}

              >{checkpoint}</div></div>
              {(this.state.checkpointIndex === checkpointIndex) ? this.state.overviewList.map((item) => {
                if (item.checkpoint !== checkpoint) {
                  return;
                }
                return (
                  <div className="teacher-overview-item" style={{ marginLeft: "40px" }} onClick={() => {
                    var lesson = item
                    this.updateAssignmentList(lesson.id)
                    //this.setState({ overviewList: lesson.challenges })
                    this.type = "assignment"
                    this.icon = <AssignmentIcon />
                    //this.updateStudentAssignment(item.name)
                  }}>
                    {this.icon} <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>{item.name}</div> {this.renderRightField(item.name)}</div>)
              }) : null}</div>)} </div>
      </div>)
    }
    if (this.type === "assignment") {
      return (<div className="teacher-overview-outer-div" style={{ height: "50vh" }}>
        <div>
          {this.state.checkpoints.map((checkpoint, checkpointIndex) =>
            <div className={(this.state.checkpointIndex === checkpointIndex) ? "checkpoint-portfolio-download" : "checkpoint-portfolio-download-closed"} style={{ backgroundColor: "transparent" }}>
              <div className="teacher-overview-item"
                onClick={() => this.setState({ checkpointIndex: checkpointIndex })}
              ><div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}

              >{checkpoint}</div></div>
              {(this.state.checkpointIndex === checkpointIndex) ? this.state.overviewList.map((item) => (
                <div onClick={() => {
                  currentAssignment = item.challenge_name
                }}>
              <Link style={{ color: "black", textDecoration: "none" }} to={this.props.link + this.type}>
                <div className="teacher-overview-item" style={{ marginLeft: "40px" }} onClick={item => this.updateStudentAssignment(item)}>
                  {this.icon} <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>{item.challenge_name}</div> {this.renderRightField(item)}</div>
                  </Link></div>
                  )) : null}</div>)} </div>
      </div>)
    }
    console.log(this.state.overviewList)


    return (<div className="teacher-overview-outer-div">
      {this.state.overviewList.map((item) => {

        return (<div onClick={() => {
          currentStudent = item
        }}><Link style={{ color: "black", textDecoration: "none" }} to={this.props.link + this.type}>
          <div className="teacher-overview-item" onClick={() => this.updateStudentAssignment(item)}>
            {this.icon}
            <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>
              {item}
            </div> {this.renderRightField(item)}
          </div>
        </Link></div>
        )
      })}
    </div>)
  }
}
