import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import { TeacherOverviewList } from "../reused-components/TeacherComponents.js";

export class TeacherOverviewStudentPage extends React.Component {
  render() {
    var link = "/teacher/class/"
    if (this.props.link) {
      link = this.props.link
    }
    return (
      <div>
        <TeacherNavbar />
        <div style={{ paddingTop: "3vh", paddingLeft: "9vw", position: "relative" }}>
          <h1>Students</h1>

          <div style={{ height: "70vh", width: "50vw", display: "inline-block", position: "relative" }}>
            {//<div style={{position: "absolute", right: 0, top: "-45px", fontWeight: 600}}>missed assignments</div>
            }
            <TeacherOverviewList type="student" showMissed={true} link={link} />
          </div >

        </div>
      </div>
    )
  }
}



